import React from "react";

function ToDoItem({ taskProps, updateItemProps}) {
  // this is the de-structuring way to pass. pass the var directly as declared in the parameter
  // and then we don't have to pass as "props.taskProps" --> "taskProps"


  
  // this is the other way, to write a function outside the return and just pass the function name in line 15
  // const changeStatus=()=>{
  //   updateItemProps(taskProps.id)
  // }
  
  return (
    <div>
      <div className="todo-item">
        <p>{taskProps.text}</p>
        <div className="btn" onClick={()=>updateItemProps(taskProps.id)}> &#10004;</div>
      </div>
    </div>
  );
}

export default ToDoItem;
