import React from "react";
import "../css/App.scss";
import Navigation from "./Navigation";
import ToDosContainer from "./ToDosContainer";
import ToDonesContainer from "./ToDonesContainer";

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      // to make this dynamic and to be able to pass all the data from parent to child, we need to store our child date in the state/props
      // so this is where the const from ToDosContainer AND ToDones go!!
      items: [
        { id: 0, text: "brush my teeth", done: false },
        { id: 1, text: "make coffee", done: false },
        { id: 2, text: "feed the dogs", done: false },
        { id: 3, text: "walk the dogs", done: false },
        { id: 4, text: "woke up", done: true },
        { id: 5, text: "drank coffee", done: true },
        { id: 6, text: "pay rent", done: true },
        { id: 7, text: "go to class", done: true },
      ],
    };
  }

  componentDidMount(){
    // below we set the item and here we are retrieving the data and storing into variable
    let storedItems = localStorage.getItem("to-do-app")
    // here we are converting into original form
    let convertedToOriginal = JSON.parse(storedItems)
    // here we store into ou state
    this.setState({
      items:convertedToOriginal
    })
  }

  

  updateItem = (id) => {
    const updatedItems = this.state.items.map((item) => {
      if (item.id === id) {
        item.done = !item.done;
        return item;
      } else {
        return item;
      }
    });
    this.setState({
      items: updatedItems,
    },()=>{
      localStorage.setItem("to-do-app", JSON.stringify(this.state.items))
    });
  };

  AddItem = (InputText) => {
    // this is the text we are getting from the user
    const item = {
      id: this.state.items.length,
      text: InputText,
      done: false,
    };

    this.setState({
      items: [...this.state.items, item],
      // the last item can be before or after, just where you want to add it on the list
    }, ()=>{
      // storing state items into localStorage
      localStorage.setItem("to-do-app", JSON.stringify(this.state.items))
    });
  };

  render() {
    const toDones = this.state.items.filter((item) => item.done === true);
    const toDos = this.state.items.filter((item) => item.done === false);

    return (
      // className = only for css styling. once we pass props (with props names, we need to edit in the child)
      <div className="app">
        <Navigation />
        <ToDosContainer
          updateItemProps={this.updateItem}
          toDosProps={toDos}
          AddItemProps={this.AddItem}
        />
        <ToDonesContainer
          updateItemProps={this.updateItem}
          toDonesProps={toDones}
        />
      </div>
    );
  }
}

export default App;

// if we want our component to be dynamic, we need a rcc. for rcc we ALWAYS need a render!
