import React from "react";
import ToDoItem from "./ToDoItem";

class ToDosContainer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      InputText: "",
    };
  }

  // now we make the method/function formSubmitted from the event in line 27
  formSubmitted = (event) => {
    event.preventDefault();
    // this is just to prevent the page from refreshing
    if (this.state.InputText.trim() !== "") {
      this.props.AddItemProps(this.state.InputText);
    }
  };

  render() {
    // console.log("render") we can see that every change on the input field, it renders again
    const todosItem = this.props.toDosProps.map((task) => {
      return (
        <ToDoItem
          key={task.id}
          updateItemProps={this.props.updateItemProps}
          taskProps={task}
        />
        //   see the ToDoItem where we passed props with this variable @ line7
      );
    });

    // separating the javascript above the return is cleaner and it's best practice. then below we just use the declared variable below
    return (
      <div className="todos-container">
        <form className="todo-form" onSubmit={this.formSubmitted}>
          <label className="input-item">
            <input
              type="text"
              name="todo"
              onChange={(event) =>
                this.setState({ InputText: event.target.value })
              }
            />
          </label>
          <input type="submit" className="btn" value="ADD" />
        </form>

        <div className="todos">
          <h3>TO DO</h3>
          {todosItem}
        </div>
      </div>
    );
  }
}

export default ToDosContainer;

// we changed this component form rfc to rcc to understand that when we want to make our input fields functional and dynamic, then we change it to rcc so we can pass state
