import React from "react";
import ToDoneItem from "./ToDoneItem";

function ToDonesContainer(props) {
  // once we pass the state, we pass props in this function as a param and use the variable we assigned in line 35/36 in App.js
  // this is only because it's a function component (rfc) so we need to mention we have to receive props

  const toDonesItem = props.toDonesProps.map((task) => {
    return (
      <ToDoneItem
        key={task.id}
        updateItemProps={props.updateItemProps}
        taskProps={task}
      />
    );
  });

  return (
    <div className="todones-container">
      <h3>BACKLOG</h3>
      {toDonesItem}
    </div>
  );
}

export default ToDonesContainer;
